// Settings
// ==========================================================================

// Change value if you want to support earlier versions of Internet Explorer.
$oldest-supported-ie-version: 9 !default;

//
// Change value if you want to controll your own margins.
$lobotomized-owl: false !default;

// Base
// ==========================================================================

// 1. Set default font family to sans-serif.
// 2. Prevent iOS and IE text size adjust after device orientation change,
//    without disabling user zoom.
// 3. Corrects text resizing oddly in IE 6/7 when body `font-size` is set using
//  `em` units.

html {
  font-family: sans-serif; // 1
  -ms-text-size-adjust: 100%; // 2
  -webkit-text-size-adjust: 100%; // 2
  @if $oldest-supported-ie-version < 8 {
    *font-size: 100%; // 3
  }
}

// Remove default margin.

body {
  margin: 0;
}
// HTML5 display definitions
// ==========================================================================

// Correct `block` display not defined for any HTML5 element in IE 8/9.
// Correct `block` display not defined for `details` or `summary` in IE 10/11
// and Firefox.
// Correct `block` display not defined for `main` in IE 11.

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}

// 1. Correct `inline-block` display not defined in IE 8/9.
// 2. Normalize vertical alignment of `progress` in Chrome, Firefox, and Opera.

audio,
canvas,
progress,
video {
  @if $oldest-supported-ie-version < 10 {
    display: inline-block; // 1
  }
  vertical-align: baseline; // 2
  @if $oldest-supported-ie-version < 8 {
    *display: inline;
    *zoom: 1;
  }
}

// Prevents modern browsers from displaying `audio` without controls.
// Remove excess height in iOS 5 devices.

audio:not([controls]) {
  display: none;
  height: 0;
}

// Address `[hidden]` styling not present in IE 8/9/10.
// Hide the `template` element in IE 8/9/11, Safari, and Firefox < 22.

@if $oldest-supported-ie-version < 11 {
  [hidden] {
    @extend %normalize-hidden-extender;
  }
}

%normalize-hidden-extender,
template {
  display: none;
}

// Links
// ==========================================================================

// Remove the gray background color from active links in IE 10.

@if $oldest-supported-ie-version < 11 {
  a {
    background-color: transparent;
  }
}

// Improve readability when focused and also mouse hovered in all browsers.

a:active,
a:hover {
  outline: 0;
};

// Text-level semantics
// ==========================================================================

// Address styling not present in IE 8/9/10/11, Safari, and Chrome.

abbr[title] {
  border-bottom: 1px dotted;
}

// Address style set to `bolder` in Firefox 4+, Safari, and Chrome.

